import react from 'react'
import { Link } from 'react-router-dom';
import Rating from '../components/Rating';
import data from '../data';


export default function HotelDetailsScreen(props){
    const hotel = data.hotels.find(x=>x._id === props.match.params.id);
    if (!hotel) {
    return <div> hotel Not Found</div>;
  }
  return (
    <div>
      <Link to="/hotels">Back to result</Link>
      <div className="row top">
        <div className="col-2">
          <img className="large" src={hotel.image} alt={hotel.name}></img>
        </div>
        <div className="col-1">
          <ul>
            <li>
              <h1>{hotel.hotelname}</h1>
            </li>
            <li>
              <Rating
                rating={hotel.rating}
                numReviews={hotel.numReviews}
              ></Rating>
            </li>
            <li>Pirce : ${hotel.price}</li>
            <li>
              Description:
              <p>{hotel.description}</p>
            </li>
          </ul>
        </div>
        <div className="col-1">
          <div className="card card-body">
            <ul>
              <li>
                <div className="row">
                  <div>Price</div>
                  <div className="price">${hotel.price}</div>
                </div>
              </li>
              <li>
                <div className="row">
                  <div>Status</div>
                  <div>
                    {hotel.countInStock > 0 ? (
                      <span className="success">In Stock</span>
                    ) : (
                      <span className="error">Unavailable</span>
                    )}
                  </div>
                </div>
              </li>
              <li>
                <button className="primary block">Add to Cart</button>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}
