import React from 'react';
import Rating from './Rating';

export default function hotel(props) {
    
  const { hotel } = props;
    return (
      <a href={`hotelDetails/${hotel._id}`}>
      <div key={hotel._id} className="card">
       
       <div className='image'><img className="medium" src={hotel.image} alt={hotel.name} /></div>
          
        
        <div className="card-body">
          <a href={`/hotel/${hotel._id}`}>
            <h2>{hotel.name}</h2>
          </a>
          <Rating
            rating={hotel.rating}
            numReviews={hotel.numReviews}
          ></Rating>
          <div className="price">${hotel.price}</div>
        </div>
       
      </div>
      </a>
     
    );
  }